body {
  display: flex;
  flex-direction: column;
  background-image: linear-gradient(coral, rgb(201, 146, 29));
}

h1,
p {
  text-align: center;
  font-weight: bolder;
}

@media (max-width: 700px) {
  html {
    font-size: 8px;
  }
}

@media (max-width: 370px) {
    html {
        font-size: 6px;
    }
}
